.quiz-header {
  padding: 6rem 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;

  @include mobile {
    padding: 3rem 0rem;
  }
  &__logo {
    display: block;
    width: 10.8rem;
    height: 3rem;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;

    position: relative;
    z-index: 99;
  }

  &__socials {
    display: flex;
    gap: 2rem;

    @include mobile {
      display: none;
    }
    a {
      display: block;
      width: 2.8rem;
      height: 2.8rem;
      color: var(--vt-white);
      transition-property: color;
      transition-duration: 0.3s;
      transition-timing-function: ease;
      svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: currentColor;
      }

      @include hover {
        color: var(--vt-black);
      }

      &:focus-visible {
        outline: none;
        color: var(--vt-black);
      }
    }
  }

  &__nav {
    @include mobile {
      display: none;
    }
  }

  &__burger {
    display: none;

    @include mobile {
      display: block;

      @include burger;
    }
  }
}
