@media (max-width: 576px) {
  .quest__content_thank {
    justify-content: center;
    gap: 0;
  }
}
.quest__quiz {
  width: 100%;
  max-width: 100%;
  min-width: 0;
}
.quest__steps {
  font-family: var(--third-family);
  font-weight: 300;
  font-size: 2.2rem;
  line-height: 110%;
  color: var(--vt-black);
}
.quest__img {
  height: 100%;
  width: 100%;
  max-width: 100%;
  min-width: 0;
}
@media (max-width: 576px) {
  .quest__img {
    max-height: 45rem;
    flex-basis: 45rem;
  }
}
.quest__img .swiper-wrapper,
.quest__img .swiper-slide {
  height: 100%;
}
.quest__img img {
  display: block;
  width: 100%;
  height: 100%;
}
.quest__thank {
  max-width: 56rem;
}