// Variables
$text-color: #fff;
$border-color: #f0f0f0;
$primary-color: #0088cc;
$primary-hover: #006ba6;

.phone-dropdown {
  position: relative;
  display: inline-block;

  &__trigger {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    cursor: pointer;
    padding: 0.8rem 1.2rem;
    user-select: none;
  }

  &__number {
    font-weight: 400;
    font-size: 2.2rem;
    color: $text-color;
    white-space: nowrap;
  }

  &__arrow {
    transition: transform 0.3s ease;
    color: $text-color;
    width: 12px;
    height: 8px;

    &--open {
      transform: rotate(180deg);
    }
  }

  &__menu {
    position: absolute;
    top: calc(100% + 1rem);
    right: 0;
    background: white;
    border-radius: 2rem;
    padding: 3rem;
    width: 38rem;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition:
      opacity 0.2s ease,
      transform 0.2s ease,
      visibility 0.2s;

    &--open {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  &__location {
    font-weight: 300;
    font-size: 2rem;
    text-align: right;
    color: #000;
  }

  &__button {
    width: 100%;
    padding: 1.4rem 4rem;
    background: $primary-color;
    color: white;
    border: none;
    border-radius: 1rem;
    font-size: 2.2rem;
    font-weight: 500;
    cursor: pointer;
    transition:
      background-color 0.2s,
      transform 0.1s;
    text-transform: lowercase;

    &:hover {
      background: $primary-hover;
    }

    &:active {
      transform: scale(0.98);
    }
  }
}

// Mobile styles
@media (max-width: 576px) {
  .phone-dropdown {
  }
}
